      return check;
    },
    dateYear () {
      return new Date().getFullYear()
    }
  },
  beforeMount () {
    if (this.isMobile) {
      document.documentElement.classList.add('touch')
    } else {
      document.documentElement.classList.add('no-touch')
    }
    this.initiatePage()
  }
